import React, {useEffect, useState} from 'react';
import {observer} from "mobx-react-lite";
import {useStores} from "../../models/root-store/root-store-context";
import './AdminFabricanteDetail.css';
import * as FIcon from "react-feather";
import {AdminItemDetail, LoaderAdmin, Page} from "../../components";
import {openPageNow} from "../../helpers";
import {RUTAS} from "../../constants";
import {DetailViewProps, useApiToaster} from "../../helpers-ts";
import {Calificacion} from "../../services/api";
import ComponentCalificaciones from "../../components/ComponenteCalificaciones";

const moment = require('moment-timezone')

const defaultValues = {
  id: 0,
  usuario: {
    first_name: '',
    last_name: '',
    nro_documento: '',
    razon_social: '',
    email: '',
    ruc: '',
    nro_telefono: '',
    imagen_perfil: ''
  },
  ciudades: [],
  vehiculos: [{
    tipo_vehiculo: {nombre: ''},
    marca: '',
    modelo: '',
    numero_chapa: '',
    ano: 0
  }],
  registro_frente: '',
  registro_dorso: '',
  registro_vencimiento: '',
  antecedentesj: '',
  antecedentesp: '',
  aprobado: false,
  rating: '',
  ratings: '',
  tipo: "Particular"
}

export const AdminTransportistaDetail: React.FC<DetailViewProps> = observer(({history, match}) => {
  const rootStore = useStores()
  const {adminStore, loginStore} = rootStore
  const [Loading, setLoading] = useState(false)
  const [Transportista, setTransportista] = useState(defaultValues as any)
  const Estado = match.params.estado
  const apiToast = useApiToaster()
  const [Calificaciones, setCalificaciones] = useState<Calificacion[]>([])
  const [Imagenes, setImagenes] = useState({
    registro_frente: null,
    registro_dorso: null,
    antecedentesj: null,
    antecedentesp: null,
  } as any)
  const idTransportista = match.params.id

  const setup = async () => {
    obtenerTiposVehiculos()
    obtenerTransportista(parseInt(idTransportista))
    obtenerCalicaciones(parseInt(idTransportista))
  }

  useEffect(() => {
    setup()
  }, [idTransportista])

  const obtenerCalicaciones = (transportista_id: number) => {
    setLoading(true)
    rootStore.environment.api.calificacionesList(transportista_id, 'transportista').then(result => {
      setLoading(false)
      if (result.kind === "ok") {
        setCalificaciones(result.data)
      }
    })
  }

  const obtenerTransportista = (idTransportista: number) => {
    rootStore.environment.api.adminGetTransportista(idTransportista).then(result => {
      if (result.kind === "ok") {
        // @ts-ignore
        setTransportista(result.transportista)
        adminStore.setTransportistaDetalle(result.transportista)
        obtenerImagenes(result.transportista)
      }
    })
  }

  const urlProtegida = (url: string) => {
    return url.replace('/media', '/mediap') + '/'
  }

  const obtenerImagenes = async (Transportista: any) => {
    const imagenes = {} as any
    let response
    if (Transportista.registro_frente) {
      response = await rootStore.environment.api.axiosGet(urlProtegida(Transportista.registro_frente), {}, {responseType: 'arraybuffer'})
      if (response.kind === 'ok') {
        imagenes.registro_frente = `data:image/jpeg;base64,` + Buffer.from(response.data, 'binary').toString('base64')
      }
    }
    if (Transportista.registro_dorso) {
      response = await rootStore.environment.api.axiosGet(urlProtegida(Transportista.registro_dorso), {}, {responseType: 'arraybuffer'})
      if (response.kind === 'ok') {
        imagenes.registro_dorso = `data:image/jpeg;base64,` + Buffer.from(response.data, 'binary').toString('base64')
      }
    }
    if (Transportista.antecedentesj) {
      response = await rootStore.environment.api.axiosGet(urlProtegida(Transportista.antecedentesj), {}, {responseType: 'arraybuffer'})
      if (response.kind === 'ok') {
        imagenes.antecedentesj = `data:image/jpeg;base64,` + Buffer.from(response.data, 'binary').toString('base64')
      }
    }
    if (Transportista.antecedentesp) {
      response = await rootStore.environment.api.axiosGet(urlProtegida(Transportista.antecedentesp), {}, {responseType: 'arraybuffer'})
      if (response.kind === 'ok') {
        imagenes.antecedentesp = `data:image/jpeg;base64,` + Buffer.from(response.data, 'binary').toString('base64')
      }
    }
    if (Transportista.constancia_ruc) {
      response = await rootStore.environment.api.axiosGet(urlProtegida(Transportista.constancia_ruc), {}, {responseType: 'arraybuffer'})
      if (response.kind === 'ok') {
        imagenes.constancia_ruc = `data:image/jpeg;base64,` + Buffer.from(response.data, 'binary').toString('base64')
      }
    }
    setImagenes(imagenes)
  }

  const editar = (row: any) => {
    openPageNow(RUTAS.ADMIN_TRANSPORTISTA_UPDATE(Estado, row.id), history)
  }

  const agregarVehiculo = () => {
    openPageNow(RUTAS.ADMIN_VEHICULO_CREATE, history)
  }

  const obtenerTiposVehiculos = () => {
    rootStore.environment.api.tiposVehiculos().then(result => {
      if (result.kind === "ok") {
        // @ts-ignore
        rootStore.setTiposVehiculos(result.data.results)
      }
    })
  }

  const aprobarTransportista = () => {
    if(!Transportista || Transportista.aprobado || ! loginStore.administrador){
      return
    }
    rootStore.environment.api.aprobarTransportista(Transportista.id).then((result: any) => {
      apiToast(result, 'El driver se aprobó exitosamente!', 'aprobar el driver')
      if(result.kind === 'ok'){
        setTransportista(Object.assign({...Transportista}, {aprobado:true}))
      }
    })
  }

  const visualizarVehiculo = (row: any) => {
    const estado = row.aprobado ? 'moderado': 'pendiente'
    openPageNow(RUTAS.ADMIN_VEHICULO_DETAIL (estado, row.id), history)
  }

  const eliminar = (row: any) => {
    // eslint-disable-next-line no-restricted-globals
    if (!confirm('Confirma que desea eliminar?')) {
      return false
    }
    rootStore.environment.api.eliminarTransportista(row.id).then((result: any) => {
      setLoading(false)
      apiToast(result, 'Eliminado satisfactoriamente', 'eliminar')
      if (result.kind === "ok") {
        history.push(RUTAS.ADMIN_TRANSPORTISTAS(Estado))
      }
    })
  }

  const renderVencimientoRegistro = (data: any) => {
    if (moment(data).diff(moment(), 'days') < 1) {
      return <span style={{color: 'red'}}
                   title={'Registro vencido'}>{moment(data).format('DD/MM/YYYY')}</span>
    } else if (moment(data).diff(moment(), 'days') < 30) {
      return <span style={{color: 'orange'}}
                   title={'Registro por vencer'}>{moment(data).format('DD/MM/YYYY')}</span>
    }
    return moment(data).format('DD/MM/YYYY')
  }

  return (
    <Page login_required admin>
      <LoaderAdmin visible={Loading}/>
      {!Loading &&<div className="page-wrapper" style={{display: "flex", height: "100vh", overflowY: "scroll"}}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">{Transportista.usuario.first_name}</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item"><a onClick={() => history.push('/admin/')}>Inicio</a></li>
                <li className="breadcrumb-item"><a
                  onClick={() => history.push(RUTAS.ADMIN_TRANSPORTISTAS(Estado))}>Drivers</a></li>
                <li className="breadcrumb-item active">{Transportista.usuario.first_name}</li>
              </ol>
            </div>
          </div>
          <div className={'mb-2 text-right'}>
            {loginStore.administrador && !Transportista.aprobado &&
            <button
                className={'btn btn-light ml-3'}
                title={'Aprobar'}
                onClick={aprobarTransportista}>
              <FIcon.Check size={14}/> Aprobar
            </button>}
            <button className={'btn btn-light ml-3'} title={'Editar'}
                    onClick={() => editar(adminStore.transportistaDetalle)}><FIcon.Edit size={14}/> Editar
            </button>
            <button className={'btn btn-light ml-3'} title={'Eliminar'}
                    onClick={() => eliminar(adminStore.transportistaDetalle)}><FIcon.Trash size={14}/> Eliminar
            </button>
          </div>
          <div className="card">
            <div className="card-body">
              <div className={'row'}>
                <AdminItemDetail titulo={'Imagen de perfil'} imagen={Transportista.usuario.imagen_perfil} columnas={3}/>
                <AdminItemDetail titulo={'Nombre'} texto={Transportista.usuario.first_name} columnas={3}/>
                <AdminItemDetail titulo={'Apellido'} texto={Transportista.usuario.last_name} columnas={3}/>
                <AdminItemDetail titulo={'Email'} texto={Transportista.usuario.email} columnas={3}/>
                <AdminItemDetail titulo={'Número de documento'} texto={Transportista.usuario.nro_documento} columnas={3}/>
                <AdminItemDetail titulo={'Número de celular'} texto={Transportista.usuario.nro_telefono} columnas={3}/>
                <AdminItemDetail titulo={'Razón social'} texto={Transportista.usuario.razon_social} columnas={3}/>
                <AdminItemDetail titulo={'RUC'} texto={Transportista.usuario.ruc} columnas={3}/>
                <AdminItemDetail titulo={'Vencimiento del registro'} texto={renderVencimientoRegistro(Transportista.registro_vencimiento)} columnas={3}/>
                <AdminItemDetail titulo={'Registro frente'} imagen={Transportista.registro_frente} es_protegido={true} columnas={3}/>
                <AdminItemDetail titulo={'Registro dorso'} imagen={Imagenes.registro_dorso}  es_protegido={true} columnas={3}/>
                <AdminItemDetail titulo={'Antecedentes judiciales'} imagen={Imagenes.antecedentesj}  es_protegido={true} columnas={3}/>
                <AdminItemDetail titulo={'Antecedentes policiales'} imagen={Imagenes.antecedentesp} es_protegido={true}  columnas={3}/>
                <AdminItemDetail titulo={'Constancia de RUC'} imagen={Imagenes.constancia_ruc} columnas={3} es_protegido={true} />
                <AdminItemDetail titulo={'Aprobado'} texto={Transportista.aprobado ? 'Si' : 'No'} columnas={3}/>
                <AdminItemDetail titulo={'Calificación'} texto={`${Transportista.rating}☆ en ${Transportista.ratings} viajes`} columnas={3}/>
                <AdminItemDetail titulo={'Ciudades en las que trabaja'} texto={Transportista.ciudades.map((ciu: any) => ciu.nombre).join(', ')} columnas={3}/>
                <AdminItemDetail titulo={'Tipo Driver'} columnas={3} texto={Transportista.tipo} />
              </div>
            </div>
          </div>
          <h4>Vehículos</h4>
          <div className="card">
            <div className="card-body">
              <div className={'mb-2 text-right'}>
                <button className={'btn btn-light'} title={'Agregar vehículo'} onClick={() => agregarVehiculo()}>
                  <FIcon.Plus size={14}/> Agregar vehículo
                </button>
              </div>
              <table className={'table'}>
                <thead>
                <tr>
                  <td>Tipo</td>
                  <td>Marca</td>
                  <td>Modelo</td>
                  <td>Año</td>
                  <td>Número de chapa</td>
                  <td>Aprobado</td>
                  <td>Acciones</td>
                </tr>
                </thead>
                <tbody>
                {Transportista.vehiculos.map((pret: any) => <tr key={`tr-key-${pret.numero_chapa}`}>
                  <td>{pret.tipo_vehiculo.nombre}</td>
                  <td>{pret.marca}</td>
                  <td>{pret.modelo}</td>
                  <td>{pret.ano}</td>
                  <td>{pret.numero_chapa}</td>
                  <td>{pret.aprobado ? "Sí" : "No"}</td>
                  <td>
                    <span className={'button'} title={'Visualizar'} onClick={() => visualizarVehiculo(pret)}>
                      <FIcon.Eye size={14}/>
                    </span>
                  </td>
                </tr>)}
                </tbody>
              </table>
            </div>
          </div>
          <ComponentCalificaciones titulo={'Driver'} calificaciones={Calificaciones}/>
        </div>
      </div>}
    </Page>
  );
});

export default AdminTransportistaDetail;
