import React from 'react'
import { observer } from 'mobx-react-lite'
import StarIcon from '@material-ui/icons/Star'
import StarBorderIcon from '@material-ui/icons/StarBorder'
import { Calificacion } from '../services/api'
import { fechahora_fmt } from '../helpers'
import { RUTAS } from '../constants'

type ComponentCalificacionesProps = {
  titulo: string
  calificaciones: Calificacion[]
}

export const ComponentCalificaciones: React.FC<ComponentCalificacionesProps> = observer((props) => {
  const nombreLabel = props.titulo === 'Proveedor' ? 'Cliente/Driver' : 'Cliente/Proveedor'
  return (
    <>
      <h4>Calificaciones del {props.titulo}</h4>
      <div className="card">
        <div className="card-body table-responsive">
          <table className={'table'}>
            <thead>
              <tr>
                <th>{nombreLabel}</th>
                <th>Pedido</th>
                <th>Fecha</th>
                <th>Calificación</th>
                <th>Comentario</th>
              </tr>
            </thead>
            <tbody>
              {props.calificaciones.length < 1 && (
                <tr>
                  <td colSpan={5}>Aún no posee calificaciones</td>
                </tr>
              )}
              {props.calificaciones.map((cal) => {
                let link = '-' as any
                if (cal.pedido_id) {
                  link = (
                    <a target={'_blank'} href={RUTAS.ADMIN_PEDIDO_DETAIL(cal.pedido_id)}>
                      #{cal.pedido_id}
                    </a>
                  )
                }
                return (
                  <tr>
                    <td>{cal.cliente}</td>
                    <td>{link}</td>
                    <td>{fechahora_fmt(cal.fecha)}</td>
                    <td>
                      <StarIcon style={{ marginRight: 5, fontSize: 15, color: '#FFA300' }} />
                      {cal.calificacion >= 2 ? (
                        <StarIcon style={{ marginRight: 5, fontSize: 15, color: '#FFA300' }} />
                      ) : (
                        <StarBorderIcon style={{ marginRight: 5, fontSize: 15, color: '#FFA300' }} />
                      )}
                      {cal.calificacion >= 3 ? (
                        <StarIcon style={{ marginRight: 5, fontSize: 15, color: '#FFA300' }} />
                      ) : (
                        <StarBorderIcon style={{ marginRight: 5, fontSize: 15, color: '#FFA300' }} />
                      )}
                      {cal.calificacion >= 4 ? (
                        <StarIcon style={{ marginRight: 5, fontSize: 15, color: '#FFA300' }} />
                      ) : (
                        <StarBorderIcon style={{ marginRight: 5, fontSize: 15, color: '#FFA300' }} />
                      )}
                      {cal.calificacion === 5 ? (
                        <StarIcon style={{ marginRight: 5, fontSize: 15, color: '#FFA300' }} />
                      ) : (
                        <StarBorderIcon style={{ marginRight: 5, fontSize: 15, color: '#FFA300' }} />
                      )}
                    </td>
                    <td>{cal.comentario}</td>
                  </tr>
                )
              })}
            </tbody>
          </table>
        </div>
      </div>
    </>
  )
})

export default ComponentCalificaciones
